

html, body {
	padding: 0;
	margin: 0;
	background: #FAFAFA;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}


@mixin clearfix {
    &:after {
        display: table;
        content: '';
        clear: both;
    }
}

@import 'breakpoints';
@import 'grid';
@import 'widths';



