.namelist ul {
  padding: 0;
  overflow: hidden;
}

.namelist li {
  display: inline-block;
  margin: 10px;
  border-radius:3px;
  box-shadow: 0 0 3px rgba(0,0,0,0.1);
}



.namelist canvas {
  margin-left: 10px;
  vertical-align: middle;
  border-bottom: 1px solid #EEE;
}



